@media only screen and (max-width: 600px) {
  .buttons {
    flex-direction: column-reverse;
    #new-quote {
      margin-bottom: 10px;
    }
  }
}
.App {
  text-align: center;
  #quote-box{
    padding: 25px;
    background-color: white;
    border-radius: 10px;
    color:black;
    max-width: 70%;

    #text {
      margin-bottom: 0;
      #quote-icon{
        margin-right: 10px;
      }
    }    
    #author{
      margin-top: 0;
      text-align: end;
    }
    .buttons{
      display: flex;
      justify-content: space-between;
      min-height: 50px;
      #tweet-quote {
        display: flex;
        padding: 10px;
        color: white;
        border-radius: 10px;
        align-self: center;
        justify-content: center;
        
      }
    
      #new-quote {
      color: white;
      border-radius: 10px;
      padding: 10px;
      cursor: pointer;
      border: none;
      font-size: 16px;
      }
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
}

